export default {
	components: {},
	data() {
		return {
			modalName: null,
			modalName1: null,
			modalName2: null,
			modalName3: null
		};
	},
	onLoad() {},
	methods: {
		endv() {
			this.modalName = null;
			this.modalName1 = null;
			this.modalName2 = null;
			this.modalName3 = null;
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.target.errMsg,
				showCancel: false
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target;
		},
		hideModal(e) {
			this.modalName = null;
			let videoContext = uni.createVideoContext('myVideo1');
			videoContext.pause();
		},
		showModal1(e) {
			this.modalName1 = e.currentTarget.dataset.target;
			let videoContext = uni.createVideoContext('myVideo2');
			videoContext.pause();
		},
		hideModal1(e) {
			this.modalName1 = null;
		},
		showModal2(e) {
			this.modalName2 = e.currentTarget.dataset.target;
		},
		hideModal2(e) {
			this.modalName2 = null;
		},
		showModal3(e) {
			this.modalName3 = e.currentTarget.dataset.target;
		},
		hideModal3(e) {
			this.modalName3 = null;
		}
	}
};
